body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.underline {
  border-bottom: 2px solid #00C0AB;
}



.genre_intro:hover {
  opacity:0.6;
  text-decoration: none;
  color:#00c0ab;
}

.genre_video:hover {
  opacity:0.6;
  text-decoration: none;
  color:#00c0ab;
}

.genre_zoom:hover {
  opacity:0.6;
  text-decoration: none;
  color:white
}

.date_head {
  color:#00C0AB;
  background-color:rgba(0,0,0,0.7);
  border-radius:8px;
  font-size:8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom:4px;
  font-weight:bold;
}

.date_box {
  color:white;
  background-color:rgba(0,0,0,0.7);
  border-radius:8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight:bold;
}


