body {
  background-color:#001D1A;
  margin-bottom:100px;
}


.mode_switch {
  transition: opacity 0.2s;
  cursor: pointer;
  cursor: hand;
}

.mode_switch:hover {
  opacity: 0.6; 
}

hr {
  margin:5px;
  margin-left:0px;
  margin-right:0px;
}

.group_filter {
  background-color:transparent;
  border:none;
  outline: none;
  color:white;
  width:300px;
  border-bottom:1px solid #aaaaaa;
  transition: border-bottom 0.2s;
  font-size:18px;
}

.group_filter:focus{
  background-color:transparent;
  border:none;
  outline: none;
  border-bottom:1px solid #ffffff;
}

.search_icon {
  cursor: pointer;
  cursor: hand;
  margin-left:8px;
  font-size:16px;
  transition: opacity 0.2s;
}

.search_icon:hover {
  opacity:60%
}


.accordion_toggle {
  background-color:rgba(0,192,171,0.2);
  cursor: pointer;
  cursor: hand;
  height: 48px;
}

.accordion_collapse {
  background-color:rgba(0,192,171,0.1);
  border-radius: 0px 0px 3px 3px;
}

.date_switch:hover {
  opacity:0.6;
}

.date_switch_pressed:hover{
  opacity: 0.6;
}

.timeline {
  overflow: auto;
  white-space: nowrap;
}

.stake {
  height:240px; 
  width:0px;
  background-color:transparent;
  border-right:2px solid rgba(255,255,255,0.5);
}


.stake_text {
  color:white;
  font-weight:bold
}

.time_card:hover {
  opacity:0.8;
}

.time_card_2p:hover {
  opacity: 0.8;
}